export const LegalFooter: React.FC = () => {
  return (
    <footer className="legal-footer" aria-label=" ">
      <nav className="container">
        <ul className="legal-footer__links">
          <li><Link to="/legal/terms"> </Link></li>
          <li><Link to="/legal/privacy"> </Link></li>
          <li><Link to="/legal/accessibility"> </Link></li>
        </ul>
      </nav>
      <div className="container">
        <p className="legal-footer__copyright">   </p>
        <p className="legal-footer__disclaimer">        /.      .</p>
      </div>
    </footer>
  );
};
