import React from 'react';
import { useParams } from 'react-router-dom';

export const LegalPage: React.FC = () => {
  const { page } = useParams<{ page: string }>();

  const contentMap: Record<string, { title: string; body: string }> = {
    terms: { title: ' ', body: '   .' },
    privacy: { title: ' ', body: '   .' },
    accessibility: { title: ' ', body: '   .' }
  };

  const data = page ? contentMap[page] || { title: ' ', body: '  .' } : { title: ' ', body: '  .' };

  return (
    <main>
      <header>
        <h1>{data.title}</h1>
      </header>
      <section>
        <p>{data.body}</p>
      </section>
    </main>
  );
};
